import { isMaxLength, isMinLength } from "../helper/validation";
import API from "../helper/api";

const insertPost = async (params) => {
  const { user } = params;

  const config = {
    headers: {
      headers: { Authorization: "Bearer " + user.token },
    },
  };

  return API.post(`/post-menager/insert-post`, params, config);
};

const getLinkData = (url) => {
  return API.post(`/post-menager/get-link-data`, {
    url,
  });
};

const removeTemponaryPhoto = (photo) => {
  return API.post(`/post-menager/photo-temponary/remove`, {
    photo,
  });
};

const uploadTemponaryPhoto = async (data) => {
  const config = {
    headers: {
      "content-type": "multipart/form-data",
    },
  };
  return API.post(`/post-menager/photo-temponary`, data, config);
};

const validationPost = (params) => {
  const { title, content, photo, isPhotoActive,isAcceptRules } = params;
  let errors = {};

  if (isMinLength(title, 1)) {
    errors["postTitle"] = "Musisz wybra tytu";
  }
  if (isMaxLength(title, 255)) {
    errors["postTitle"] = "Wybrany tytu jest z dugi";
  }
  if (isPhotoActive) {
    if (isMinLength(photo, 0)) {
      errors["postPhoto"] = "Musisz doczy zdjcie";
    }
  }
  if (isMinLength(content, 0)) {
    errors["postContent"] = "Musisz doczy opis";
  }
  if (!isAcceptRules) {
    errors["postIsAcceptRules"] = "Musisz zakceptowa regulamin";
  }


  return { isValid: Object.keys(errors).length == 0 ? true : false, errors };
};
const validationGraphic = (params) => {
  const { title, photo, isTitleTop, titleTop,isAcceptRules } = params;
  let errors = {};

  if (isMinLength(title, 1)) {
    errors["graphicTitle"] = "Musisz wybra tytu";
  }

  if (isTitleTop) {
    if (isMinLength(titleTop, 1)) {
      errors["graphicTitleTop"] =
        "Jeli zaznaczye grny tytu musisz go wpisa";
    }
  }

  if (isMinLength(photo, 1)) {
    errors["graphicPhoto"] = "Musisz wybra zdjcie";
  }
  console.log('isAccept',isAcceptRules);
  if (!isAcceptRules) {
    errors["graphicIsAcceptRules"] = "Musisz zakceptowa regulamin";
  }

  return { isValid: Object.keys(errors).length == 0 ? true : false, errors };
};

const validationYoutube = (params) => {
  const { title, youtube,isAcceptRules } = params;
  let errors = {};

  if (isMinLength(title, 1)) {
    errors["youtubeTitle"] = "Musisz wybra tytu";
  }

  if (isMinLength(youtube, 1)) {
    errors["youtubeLink"] = "Musisz wklei poprawny link";
  }

  if (!isAcceptRules) {
    errors["youtubeIsAcceptRules"] = "Musisz zakceptowa regulamin";
  }

  return { isValid: Object.keys(errors).length == 0 ? true : false, errors };
};


const validationLink = (params) => {
  const { title, content, link, linkPhoto,isCorrectLink } = params;
  let errors = {};

  if (isCorrectLink) {
    errors["link"] = "Wybrany link jest nieprawidowy";
  }
  if (isMinLength(link, 1)) {
    errors["link"] = "Musisz wybra link";
  }
  if (isMinLength(title, 1)) {
    errors["linkTitle"] = "Musisz wybra tytu";
  }
  if (isMaxLength(title, 255)) {
    errors["linktitle"] = "Wybrany tytu jest z dugi";
  }
  return { isValid: Object.keys(errors).length == 0 ? true : false, errors };
};

const postMenagerServices = {
  validationPost,
  validationLink,
  validationYoutube,
  validationGraphic,
  getLinkData,
  uploadTemponaryPhoto,
  removeTemponaryPhoto,
  insertPost,
};

export default postMenagerServices;
